.pen-drawing-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.pen-drawing-panel {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  width: 95vw;
  max-width: 900px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid #e0e0e0;
  background: #f8f9fa;
  border-radius: 8px 8px 0 0;

  h3 {
    margin: 0;
    font-size: 18px;
    font-weight: 600;
    color: #333;
  }

  .close-btn {
    background: none;
    border: none;
    padding: 4px;
    cursor: pointer;
    color: #666;
    font-size: 16px;
    border-radius: 4px;

    &:hover {
      background: #e9ecef;
      color: #333;
    }
  }
}

.panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: grid;
  grid-template-columns: 280px 1fr;
  grid-gap: 20px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;

  h4 {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
    color: #555;
  }

  .canvas-actions {
    display: flex;
    gap: 8px;
  }
}

// 왼쪽 컬럼 - 도구 설정
.tools-section {
  .section-header {
    border-bottom: 2px solid #e9ecef;
    padding-bottom: 8px;
    margin-bottom: 16px;
  }
}

.tool-controls {
  .control-group {
    margin-bottom: 24px;

    label {
      display: block;
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 500;
      color: #555;
    }
  }

  .color-picker-group {
    display: flex;
    flex-direction: column;
    gap: 8px;

    .color-input {
      width: 60px;
      height: 40px;
      border: 1px solid #ccc;
      border-radius: 4px;
      cursor: pointer;
    }

    .color-presets {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 6px;

      .color-preset {
        width: 40px;
        height: 40px;
        border: 2px solid #e0e0e0;
        border-radius: 6px;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
          transform: scale(1.05);
          border-color: #007bff;
        }

        &.active {
          border-color: #007bff;
          box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
        }
      }
    }
  }

  .stroke-width-options {
    display: flex;
    gap: 4px;
    flex-wrap: wrap;

    button {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 8px 12px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: white;
      cursor: pointer;
      transition: all 0.2s;
      min-width: 40px;
      height: 40px;

      &:hover {
        border-color: #007bff;
        background: #f8f9ff;
      }

      &.active {
        border-color: #007bff;
        background: #007bff;
        
        .width-preview {
          background: white;
        }
      }

      .width-preview {
        width: 20px;
        background: #333;
        border-radius: 2px;
      }
    }
  }

  .tool-buttons {
    display: flex;
    gap: 8px;

    .eraser-btn {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 4px;
      padding: 12px 16px;
      border: 2px solid #e0e0e0;
      border-radius: 6px;
      background: white;
      cursor: pointer;
      transition: all 0.2s;
      flex: 1;

      &:hover {
        border-color: #dc3545;
        background: #fff5f5;
      }

      &.active {
        border-color: #dc3545;
        background: #f8d7da;
        color: #dc3545;
      }

      i {
        font-size: 18px;
      }

      span {
        font-size: 12px;
        font-weight: 500;
      }
    }
  }
}

// 오른쪽 컬럼 - 캔버스
.canvas-section {
  .canvas-actions {
    button {
      padding: 6px 12px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: white;
      cursor: pointer;
      transition: all 0.2s;
      font-size: 12px;

      &:hover:not(:disabled) {
        border-color: #007bff;
        background: #f8f9ff;
      }

      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }

      &.undo-btn {
        color: #007bff;
        
        &:hover:not(:disabled) {
          background: #e3f2fd;
        }
      }

      &.clear-btn {
        color: #dc3545;
        
        &:hover:not(:disabled) {
          background: #f8d7da;
        }
      }

      i {
        margin-right: 4px;
      }
    }
  }
}

.canvas-container {
  position: relative;
  border: 2px solid #e0e0e0;
  border-radius: 6px;
  background: white;
  height: 450px;

  .drawing-canvas {
    width: 100%;
    height: 100%;
    cursor: crosshair;
    border-radius: 4px;
    touch-action: none; // 터치 스크롤 방지
  }

  .canvas-help {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #999;
    pointer-events: none;

    p {
      margin: 0 0 12px 0;
      font-size: 14px;
    }

    .help-icons {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      font-size: 16px;

      span {
        font-size: 12px;
      }
    }
  }
}

.drawing-stats {
  display: flex;
  gap: 16px;
  padding: 12px 20px;
  background: #f8f9fa;
  border-top: 1px solid #e0e0e0;

  .stats-item {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 14px;
    color: #666;

    i {
      color: #007bff;
    }
  }
}

.panel-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-top: 1px solid #e0e0e0;
  background: #f8f9fa;
  border-radius: 0 0 8px 8px;

  .footer-info {
    font-size: 14px;
    color: #666;
  }

  .footer-actions {
    .cancel-btn {
      padding: 8px 16px;
      border: 1px solid #ccc;
      border-radius: 4px;
      background: white;
      color: #666;
      cursor: pointer;
      transition: all 0.2s;

      &:hover {
        background: #f8f9fa;
        border-color: #999;
      }
    }
  }
}

// 반응형 디자인
@media (max-width: 1024px) {
  .panel-content {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
  }
  
  .canvas-container {
    height: 350px;
  }
}

@media (max-width: 768px) {
  .pen-drawing-panel {
    width: 98vw;
    max-height: 95vh;
  }

  .tool-controls {
    .color-picker-group .color-presets {
      grid-template-columns: repeat(3, 1fr);
    }

    .stroke-width-options {
      justify-content: center;
    }
  }

  .canvas-container {
    height: 300px;
  }

  .panel-content {
    padding: 16px;
  }

  .drawing-stats {
    flex-direction: column;
    gap: 8px;
  }
}